import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { CookieService } from "ngx-cookie-service";
import { AuthService } from "../../services/auth.service";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  public registerFormGroup: FormGroup;

  constructor(private cookieService: CookieService, private formBuilder: FormBuilder, private authService: AuthService) {
    this.registerFormGroup = this.formBuilder.group({
      _csrf: [''],
      email: [''],
      password: [''],
    });
  }


  async ngOnInit(): Promise<void> {
    const csrfToken = await this.authService.getCsrfToken();
    console.log(csrfToken);
    this.registerFormGroup.get('_csrf').setValue(csrfToken);
  }

  async onSubmit() {
    //console.log(this.registerFormGroup.value);
    const json = JSON.parse(JSON.stringify(this.registerFormGroup.value));
    console.log(json);
    await this.authService.register(json);
  }

}
