import {Injectable} from '@angular/core';
import {HttpClient, HttpParams, HttpRequest} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private http: HttpClient){

  }

  public async getCsrfToken(){
    const resp: any = await this.http.get('/api/get-csrf-token').toPromise();
    return resp.token;
  }

  public async register(formData): Promise<any> {
    return await this.http.post('/api/register', formData).toPromise();
  }

  public async profile(){
    return await this.http.get('/api/profile').toPromise();
  }
}
