import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CommonModule} from '@angular/common';
import {LoginComponent} from './routes/login/login.component';
import {RegisterComponent} from './routes/register/register.component';
import {LogoutComponent} from './routes/logout/logout.component';
import {ProfileComponent} from './routes/profile/profile.component';
import {HomeComponent} from "./routes/home/home.component";

const routes: Routes = [
  {
    path: '',
    component: HomeComponent, data: { name: 'home' }
  },
  {
    path: 'login',
    component: LoginComponent, data: { name: 'login' }
  },
  {
    path: 'register',
    component: RegisterComponent, data: { name: 'register' }
  },
  {
    path: 'logout',
    component: LogoutComponent, data: { name: 'logout' }
  },
  {
    path: 'profile',
    component: ProfileComponent, data: { name: 'profile' }
  },
];

@NgModule({
  declarations: [],
  imports: [RouterModule.forRoot(routes, {
    initialNavigation: 'enabled'
  }),
  CommonModule
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
